import * as d3 from 'd3';

const colors = ['#2ECC71', '#3498DB', '#9B59B6'];

const drawLineGraph = (array, container, size) => {
    const { data, metric } = array[0];
    let width = document.getElementById(size).offsetWidth;
    let height = (size == "section" ? width / 4: width * 0.9);
    const margin = { top: 5, right: 75, bottom: 50, left: 75 };

    let svg = d3.select(container);
    
    svg.selectAll("*").remove();
    svg = d3.select(container)
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform",
            "translate(" + margin.left + "," + margin.top + ")");
    
    // Add x-axis
    let scaleX = d3.scaleTime()
        .domain([d3.min(data, (d) => { return d.date; }), d3.max(data, function(d) { return d.date; })])
        .range([ 0, width - margin.right ]);
    svg.append("g")
        .attr("class", "axis")
        .attr("transform", "translate(0," + height + ")")
        .call(d3.axisBottom(scaleX).ticks(5));

    // Add y-axis
    let scaleY = d3.scaleLinear()
        .domain([0, d3.max(data, (d) => { return +d[metric]; })])
        .range([height, 0 ]);

    svg.append("g")
        .attr("class", "axis")
        .call(d3.axisLeft(scaleY).ticks(10));

    // // Add the path(s) using this helper function
    for (let i = 0; i < array.length; i++) {
        
        // Prepare a helper function
        let lineFunc = d3.line()
            .x((d) => { return scaleX(d.date) })
            .y((d) => { return scaleY(d[array[i].metric]) })

        let path = svg.append('svg:path')
            .attr('d', lineFunc(array[i].data))
            .attr('stroke', colors[i])
            .attr("stroke-width", 2)
            .attr("stroke-linejoin", "round")
            .attr("stroke-linecap", "round")
            .attr('fill', 'none')
            .attr("id", (d) => { return ("path_" + i)});
    
        let totalLength = path.node().getTotalLength();

        path
            .transition()
                .duration(1000)
                .ease(d3.easeCubic)
                .attr("stroke-dashoffset", 0);

        path
            .attr("stroke-dasharray", totalLength + " " + totalLength)
            .attr("stroke-dashoffset", totalLength)
            .transition()
                .duration(1000)
                .ease(d3.easeQuad)
                .attr("stroke-dashoffset", 0);
    }

    var box = document.getElementById('test');
    var node = d3.select('#path_0').node();
    var pathLength = node.getTotalLength();

    // var circle = svg.append("circle")
    //                 .attr("cx", 0)
    //                 .attr("cy", node.getPointAtLength(0))
    //                 .attr("r", 10)
    //                 .attr("fill", "none")
    //                 .attr('stroke', colors[0])
    //                 .attr("stroke-width", 2);

    // box.addEventListener('mousemove', e => {
    //     var leftOffset = width + document.getElementById(size).offsetLeft + margin.left + 10;
    //     var x = e.x - leftOffset;
    //     let y = node.getPointAtLength((pathLength * (x / (width + margin.left)))).y;

    //     console.log(`Path length: ${pathLength}`);
    //     console.log(`x Position: ${x}`);
    //     console.log(`Width: ${width + margin.left + margin.right}`);
    //     console.log('Point at x = 0:');
    //     console.log(node.getPointAtLength(0));
    //     if (x >= 0 && x < (width - margin.right)) {
    //         circle
    //         .attr("cx", x)
    //         .attr("cy", y);
    //     }
    // });
}

const drawBarGraph = (array, container, size) => {
    const { data, metric } = array[0];
    let width = document.getElementById(size).offsetWidth;
    let height = (size == "section" ? width / 4: width * 0.9);
    const margin = { top: 5, right: 75, bottom: 50, left: 75 };

    let svg = d3.select(container);
        svg.selectAll("*").remove();

    
    console.log(svg);
    svg = d3.select(container)
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform",
            "translate(" + margin.left + "," + margin.top + ")");

    // Add x-axis
    let scaleX = d3.scaleBand()
    .range([ 0, width - margin.right ])
    .padding(0.5);

    svg.append("g")
        .attr("class", "axis")
        .attr("transform", "translate(0," + height + ")")
        .call(d3.axisBottom(scaleX).ticks(5));

    // Add y-axis
    let scaleY = d3.scaleLinear()
        .domain([0, d3.max(data, (d) => { return +d[metric]; })])
        .range([height, 0 ]);

    svg.append("g")
        .attr("class", "axis")
        .call(d3.axisLeft(scaleY).ticks(10));

    // // Add the path(s) using this helper function
    for (let i = 0; i < array.length; i++) {
        
        // Prepare a helper function
        let lineFunc = d3.line()
            .x((d) => { return scaleX(d.date) })
            .y((d) => { return scaleY(d[array[i].metric]) })

        let path = svg.append('path')
        .attr('d', lineFunc(array[i].data))
        .attr('stroke', colors[i])
        .attr("stroke-width", 2)
        .attr("stroke-linejoin", "round")
        .attr("stroke-linecap", "round")
        .attr('fill', 'none');
    
        let totalLength = path.node().getTotalLength();

        path
            .transition()
                .duration(1000)
                .ease(d3.easeCubic)
                .attr("stroke-dashoffset", 0);

        path
            .attr("stroke-dasharray", totalLength + " " + totalLength)
            .attr("stroke-dashoffset", totalLength)
            .transition()
                .duration(1000)
                .ease(d3.easeQuad)
                .attr("stroke-dashoffset", 0);
    }
}

export { drawLineGraph, drawBarGraph };