    async handleKeyDown(e) {
        const stockTicker = e.target.value.toUpperCase();
        if (e.key === 'Enter') {
            const stockInfo = await getStockInfo(stockTicker);
            console.log(stockInfo);
            if (stockInfo) {
                this.setState({
                    ...this.state,
                    stockTicker,
                    stockInfo
                }, () => {
                    const { stockHistory, incomeStatement  , balanceSheet, dCF, financialRatios } = stockInfo;
                    drawLineGraph(
                        [{ data: stockHistory, metric: "close" }], 
                        ".stock-overview-chart-container", 
                        "section-half"
                    );

                    if (incomeStatement) {
                        const revenue = financialGrowthExtract(incomeStatement.reverse(), "Revenue");
                        const netIncome = financialGrowthExtract(incomeStatement.reverse(), "Net Income");
                        drawLineGraph(
                            [
                                { data: revenue, metric: "Revenue"},
                                { data: netIncome, metric: "Net Income"},
                            ], 
                            ".stock-income-chart-container", 
                            "section-half"
                        );
                    }

                    if (balanceSheet) {
                        const totalAssets = financialGrowthExtract(balanceSheet.reverse(), "Total assets");
                        const totalLiabilities = financialGrowthExtract(balanceSheet.reverse(), "Total liabilities");
                        drawLineGraph(
                            [
                                { data: totalAssets, metric: "Total assets"},
                                { data: totalLiabilities, metric: "Total liabilities"},
                            ], 
                            ".stock-balance-chart-container", 
                            "section-half"
                        );
                    }

                    // if (dCF) {
                    //     drawBarGraph([
                    //         {data: }
                    //     ], ".stock-dcf-chart-container", "section-half");
                    // }
                });
            }
        }
    }

