const stockHistoryReformat = (arr) => {
    let stockHistory = [];
    for (let i = 0; i < arr.length; i++) {
        const stockDatum = {
            date: new Date(arr[i].date.substring(0, 4), arr[i].date.substring(5, 7), arr[i].date.substring(8, 10)),
            close: arr[i].close
        }
        stockHistory.push(stockDatum);
    }
    return stockHistory;
}

const financialGrowthExtract = (arr, metric) => {
    let financialGrowth = [];
    for (let i = 0; i < arr.length; i++) {
        const financialDatum = {
            date: new Date(arr[i].date.substring(0, 4), arr[i].date.substring(5, 7), arr[i].date.substring(8, 10)),
            [metric]: parseFloat(arr[i][metric])/1000000
        }
        financialGrowth.push(financialDatum);
    }
    return financialGrowth;
}

export { stockHistoryReformat, financialGrowthExtract };