import React from 'react';
import numeral from 'numeral';

export default ({ stockInfo }) => {
    return (
        <div>
            {stockInfo.incomeStatement && (
                <div className="section">
                <h4>INCOME STATEMENT </h4>
                <div className="layout-flex">
                    <div className="section-half marg-r-sm">
                        <p>Revenue: <strong>{stockInfo.incomeStatement ? numeral(parseFloat(stockInfo.incomeStatement[0]["Revenue"]).toFixed(2)).format('$0,0.00') : "N/A"}</strong></p>
                        <p>Cost of Revenue: <strong>{stockInfo.incomeStatement ? numeral(parseFloat(stockInfo.incomeStatement[0]["Cost of Revenue"]).toFixed(2)).format('$0,0.00') : "N/A"}</strong></p>
                        <p>Gross Profit: <strong>{stockInfo.incomeStatement ? numeral(parseFloat(stockInfo.incomeStatement[0]["Gross Profit"]).toFixed(2)).format('$0,0.00') : "N/A"}</strong></p>
                        <p>Net Income: <strong>{stockInfo.incomeStatement[0]["Net Income"] ? numeral(parseFloat(stockInfo.incomeStatement[0]["Net Income"]).toFixed(2)).format('$0,0.00') : "N/A"}</strong></p>
                    </div>
                    <div className="section-half">
                        <div className="stock-income-chart-container" />
                    </div>
                </div>
            </div>
            )}
        </div>
    );
}